<template>
  <a-space direction="vertical" fill>
    <div class="container">

      <a-tag v-if="false" color='arcoblue'>Tag</a-tag>
      <a-input :style="{ width: '260px' }" default-value="content" v-model="course_id" placeholder="输入您讲授的课程ID"
        class="input-field" type="text" allow-clear />
      <a-space>
        <a-tag v-if="false" color='arcoblue'>Tag</a-tag>
        <a-button type="primary" shape="round" @click="submit">进入打分</a-button>
        <a-button type="primary" shape="round" @click="analyse">成绩分析</a-button>

      </a-space>



    </div>

  </a-space>
</template>
  
<script>
import teacher_api from "@/api/teacher/teacher-api"
import authApi from "@/api/auth-api";

export default {
  data() {
    return {
      errorMessage: "",
      course_id: "",
      user_id: "",
      data: []
    };
  },

  mounted() {
    this.user_id = authApi.getcookie("user_id")
    console.log(this.user_id)
  },


  methods: {
    set_err_msg(msg) {
      this.errorMessage = msg
    },
    submit() {
      teacher_api.giveScoreForm({
        user_id: this.user_id,
        course_id: this.course_id
      }).then(resp => {

        if (resp.status_code != 200) {
          if (resp.status_code == 401) {
            this.$message("身份验证失败")
          }
          else {
            this.$message(resp.status_text + " / " + resp.data)
          }
          return
        }

        if (resp.data) {
          if (resp.data.length == 0) {
            this.$message.info("该课号没有需要打分的学生")
          }
          else {
            let query_data = {
              course_id: this.course_id,
            }

            this.$router.push({ path: '/teacher/giveScore', query: query_data })
          }

        } else {
          this.$message.info(resp.data)
        }

      }).catch(err => {
        if (err.code === "ERR_NETWORK") {
          this.$message("网络错误")
        }
        else {
          this.$message(err.message)
        }

      })

    },

    analyse() {
      teacher_api.giveScoreForm({
        user_id: this.user_id,
        course_id: this.course_id
      }).then(resp => {

        if (resp.status_code != 200) {
          if (resp.status_code == 401) {
            this.$message("身份验证失败")
          }
          else {
            this.$message(resp.status_text + " / " + resp.data)
          }
          return
        }

        if (resp.data) {
          if (resp.data.length == 0) {
            this.$message.info("该课号没有需要分析的学生成绩")
          }
          else {
            let query_data = {
              course_id: this.course_id,
            }

            this.$router.push({ path: '/teacher/scoreAnalyse', query: query_data })
          }

        } else {
          this.$message.info(resp.data)
        }
      }).catch(err => {
        if (err.code === "ERR_NETWORK") {
          this.$message("网络错误")
        }
        else {
          this.$message(err.message)
        }

      })

    },
  }
};
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  /* 使整个容器垂直居中 */
}

.input-field {
  margin-bottom: 10px;
  /* 输入框下方的间距 */
}
</style>